body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f9;
    margin: 0;
    padding: 0;
  }
  
  .container {
    width: 300px;
    margin: 50px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  h2 {
    text-align: center;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }
  
  input {
    width: 100%;
    padding: 8px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button {
    width: 100%;
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  #registerMessage, #loginMessage {
    text-align: center;
    margin-top: 10px;
    color: red;
  }
  /* Style for the "Sign Up" link */
a {
  text-decoration: none;
  color: white;
  background-color: #4CAF50;
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 16px;
  display: inline-block;
  margin-top: 15px;
}

a:hover {
  background-color: #45a049;
}

  